/* blue = f2e9e4; */
:root {
  --beige: #f2e9e4;
  --darkbeige: #c9ada7;
  --brown: #9a8c98;
  --blue: #4a4e69;
  --navy: #22223b;
}

html {
  background-color:var(--beige);
}

.body {
  display: flex;
  width:100vw;
  height:100vw;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  
}

h1{
  color:#4a4e69;
  margin-top: 30px;
}

h2{
  color:#4a4e69;
  margin: 0px;
}

h3{
  display: flex;
  color:white;
  margin: 30px;
  align-self:center;
  justify-self: center;
}

.textbox {
  display:flex;
  flex-direction: column;
  background-color:#4a4e69;
  width: 60%;
  margin: 30px;

}
p {
  margin:10px;
  color: white;
}